import isEmpty from "./isEmpty"
const validate = require('validate.js')


const validateVendorCreation = (vendorData) => {


    const errors = {}

    if (isEmpty(vendorData.fname)) {
        errors.fname = 'First Name cannot be empty.'
    }

    if (isEmpty(vendorData.lname)) {
        errors.lname = 'Last Name cannot be empty.'
    }

    if (isEmpty(vendorData.email)) {
        errors.email = 'Email cannot be empty.'
    }



    if (vendorData.email && !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(vendorData.email)) {
        errors.email = 'Email not valid.'

    }

    if (isEmpty(vendorData.phone1)) {
        errors.phone1 = 'Atleast, a phone number should be provided.'
    }
    if (isEmpty(vendorData.country)) {
        errors.country = 'Provide vendor\'s country name.'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

export { validateVendorCreation }